.status-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

.status__text {
    font-size: 2rem;
    letter-spacing: .1rem;
    margin-bottom: 2rem;
}

.status__text span {
    font-weight: bold;
}

.status__light {
    height: 10rem;
    width: 10rem;
    border-radius: 50%;
    -webkit-transition: all 300ms;
    transition: all 300ms;
}

.status__light--Failed {
    background-color: hsla(0, 50%, 50%, 1);
    box-shadow: 0 0 6rem 0 hsla(0, 50%, 50%, 1);
    border: 5px solid hsla(0, 50%, 48%, 1);
}

.status__light--Success {
    background-color: hsla(120, 50%, 50%, 1);
    box-shadow: 0 0 6rem 0 hsla(120, 50%, 50%, 1);
    border: 5px solid hsla(120, 50%, 48%, 1);
}

.status__light--Unknown {
    background-color: hsla(120, 10%, 70%, 1);
    box-shadow: 0 0 6rem 0 hsla(120, 10%, 70%, 1);
    border: 5px solid hsla(120, 10%, 70%, 1);
}

.app {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 1fr;
    grid-gap: 1rem;
}

@media (max-width: 32rem) {
    .app {
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 22rem) {
    .app {
        grid-template-columns: 1fr;
    }
}
body, html {
    font: 100% / 1.5;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

*, *::after, *::before {
    box-sizing: border-box;
}

#root, html, body {
    height: 100vh;
    width: 100vw;
}
